@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
html,
body {
  font-family: "Roboto", sans-serif; }

* {
  padding: 0;
  margin: 0;
  outline: none;
  box-sizing: border-box; }

.App {
  height: 100vh; }

.MainWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: stretch;
          align-items: stretch;
  margin: 50px 0; }

.Header {
  text-align: center;
  padding: 10px 0;
  background: indianred;
  color: white; }
  .Header__title {
    font-weight: 300;
    letter-spacing: 3px;
    text-shadow: 2px 3px 5px rgba(0, 0, 0, 0.5); }

.Shop {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 500px; }

.ShopItem {
  height: 100px;
  width: 100px;
  margin: 10px;
  background: indianred;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 5px;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.5);
  padding: 0 5px;
  text-align: center; }
  .ShopItem__title {
    font-weight: 400; }
  .ShopItem__category {
    margin: 3px 0;
    text-transform: capitalize;
    font-weight: 300; }

.Cart {
  border: 2px solid indianred;
  border-radius: 5px;
  width: 300px;
  position: relative; }
  .Cart__header {
    background: indianred;
    color: white;
    font-size: 25px;
    font-weight: 300;
    padding: 5px;
    text-align: center; }
  .Cart__items {
    padding: 5px; }
  .Cart__item {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 2px 5px;
    border: 1px solid darkred;
    border-radius: 5px;
    margin: 5px 0; }
    .Cart__item--name {
      font-size: 20px;
      color: indianred;
      font-weight: 700; }
    .Cart__item--price {
      font-weight: bold;
      color: indianred;
      font-size: 20px; }
  .Cart__footer {
    background: darkred;
    color: white;
    font-size: 25px;
    font-weight: 300;
    padding: 5px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between; }
    .Cart__footer--price {
      font-weight: bold; }

