.Shop {
  display: flex;
  flex-wrap: wrap;
  width: 500px;
}

.ShopItem {
  height: 100px;
  width: 100px;
  margin: 10px;
  background: indianred;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.5);
  padding: 0 5px;
  text-align: center;

  &__title {
    font-weight: 400;
  }

  &__category {
    margin: 3px 0;
    text-transform: capitalize;
    font-weight: 300;
  }
}
