.Cart {
  border: 2px solid indianred;
  border-radius: 5px;
  width: 300px;
  position: relative;

  &__header {
    background: indianred;
    color: white;
    font-size: 25px;
    font-weight: 300;
    padding: 5px;
    text-align: center;
  }

  &__items {
    padding: 5px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2px 5px;
    border: 1px solid darkred;
    border-radius: 5px;
    margin: 5px 0;

    &--name {
      font-size: 20px;
      color: indianred;
      font-weight: 700;
    }
    &--price {
      font-weight: bold;
      color: indianred;
      font-size: 20px;
    }
  }

  &__footer {
    background: darkred;
    color: white;
    font-size: 25px;
    font-weight: 300;
    padding: 5px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &--price {
      font-weight: bold;
    }
  }
}
